
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'

import applyFooter from 'components/apply/apply-footer.vue'
import {applyQuery, applyCreate, recall} from '@/api/apply-detail'

export default {
name: 'applyOvertime',
components: {Datetime, Group, applyFooter, AlertPlugin},
data() {
return {
applyStatus: "1",
params: {
hrWorkflowId: "", //id
fid: "",
status: "1",
overtimeStart: "",
overtimeEnd: "",
overtimeReason: "",
}
}
},
methods: {
save(){
if (this.overtimeStart === "") {
this.$vux.alert.show({
content: '',
});
} else if (this.overtimeEnd === "") {
this.$vux.alert.show({
content: '',
});
} else if (this.overtimeReason === "") {
this.$vux.alert.show({
content: '',
});
} else {
this.$router.push({
name: 'apply',
params: {
"hrWorkflowType": "",
"hrWorkflowTypeDetail": "",//
"hrStartTime": this.overtimeStart,//
"hrEndTime": this.overtimeEnd,//
"hrWorkflowReason": this.overtimeReason,//
"hrWorkflowNode": "1"
}
});
}
},
saveOrSubmit(stateFlag){
if (this.params.overtimeStart === "") {
this.$vux.alert.show({
content: '',
});
} else if (this.params.overtimeEnd === "") {
this.$vux.alert.show({
content: '',
});
} else if (this.params.overtimeReason === "") {
this.$vux.alert.show({
content: '',
});
} else {
if (stateFlag === 'save') {
this.params.fid = "";
this.$vux.loading.show({text: ''});
} else {
this.$vux.loading.show({text: ''});
}

applyCreate({
"hrWorkflowId": this.params.hrWorkflowId,
"hrWorkflowType": "applyOvertime",
"hrWorkflowTypeDetail": "",                   //
"hrStartTime": this.params.overtimeStart,         //
"hrEndTime": this.params.overtimeEnd,             //
"hrWorkflowReason": this.params.overtimeReason,   //
"hrWorkflowNode": this.params.fid,
"hrWorkflowNodeStatus": "1",
}).then(response => {
if (response.data && response.data.rows) {
this.$vux.loading.hide();
if (stateFlag === 'save') {
this.$vux.toast.show({text: ''});
} else {
this.$vux.toast.show({text: ''});
window.localStorage.setItem('applyStatus', "2");
}
let _this = this;
setTimeout(function () {
_this.$router.go(-1);
}, 1600);
} else {
this.$vux.loading.hide();
if (stateFlag === 'save') {
this.$vux.toast.show({text: ''});
} else {
this.$vux.toast.show({text: ''});
}
}
}).catch(error => {
this.$vux.loading.hide();
});
}
},
revoke(){
this.$vux.toast.show({text: ''});
recall({"hrWorkflowId": this.params.hrWorkflowId}).then(response => {
if (response.data && response.data.success) {
this.$vux.loading.hide();
this.$vux.toast.show({text: ''});
window.localStorage.setItem('applyStatus', "1");
let _this = this;
setTimeout(function () {
_this.$router.go(-1);
}, 1600);
}
}).catch(error => {
this.$vux.loading.hide();
});
}
},
mounted() {

},
created() {

if (this.$route.query.item) {
let detailsItem = this.$route.query.item;
this.params.hrWorkflowId = detailsItem.hrWorkflowId;
this.params.hrApplyType = detailsItem.hrWorkflowTypeDetail;
this.params.overtimeStart = detailsItem.hrStartTime;
this.params.overtimeEnd = detailsItem.hrEndTime;
this.params.overtimeReason = detailsItem.hrWorkflowReason;
}
if (this.$route.query.applyStatus === '1') {
this.applyStatus = "1";
} else if (this.$route.query.applyStatus === '2') {
this.applyStatus = "2";
} else if (this.$route.query.applyStatus === '3') {
this.applyStatus = "3";
}
}
}



